body {
  font-family: 'Segoe UI', sans-serif;
  background-color: #f0f2f5;
  margin: 0;
  padding: 20px;
}

.container {
  max-width: 600px;
  margin: auto;
  background: white;
  border-radius: 12px;
  padding: 30px;
  box-shadow: 0 0 20px rgba(0,0,0,0.1);
}

h1 {
  text-align: center;
  color: #333;
}

.selector {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

select, button {
  padding: 10px;
  font-size: 16px;
  border-radius: 8px;
  border: 1px solid #ccc;
}

button {
  background-color: #007bff;
  color: white;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}

.output {
  margin-top: 30px;
  font-size: 18px;
}

.step {
  display: flex;
  align-items: center;
  gap: 10px;
  margin: 10px 0;
}

.step i {
  color: #007bff;
}

.popup {
  background-color: #e8f4ff;
  border: 1px solid #a6d4fa;
  color: #055160;
  padding: 12px;
  border-radius: 8px;
  font-weight: 600;
  margin-bottom: 20px;
  text-align: center;
}
